import {
    SOUNDS_GET_ALL,
} from './action-types';

export function startGetAllSounds() {
    return {
        action: SOUNDS_GET_ALL
    }
}

export function getAllSounds(file) {
    return function (dispatch, getState) {
        const { keywords } = getState().general

        const searchTerm = "things"
        const url = `https://freesound.org/apiv2/search/text/?query=${searchTerm}&token=FTDBgkb5Q3NWqdrtVvNzXNqxIu9TFhj1qrWl2Ue9`
        fetch(url)
            .then((response) => response.json())
            .then((json) => {
                console.log("SUCCESS", json);
            })
            .catch((err) => {
                console.error("ERROR", err);
            });
    }
}
  


